<template>
  <img src="https://i.imgur.com/nsQn24zm.jpg" @mousedown="mousedown()"  >
</template>

<script>

// @ is an alias to /src
    export default { 
        data() {
            return {
                time:0,
                firstTime:0,
            }
        },
        methods: {
            mousedown() {
                console.log("滑鼠點擊")
                this.firstTime = new Date().getTime()
                this.timeOut = setTimeout(() => {
                  console.log("長按事件")
                  this.saveImg()
                }, 800 )
            },
            saveImg() {
              axios({
                        url: 'https://i.imgur.com/nsQn24zm.jpg',
                        method: 'GET',
                        responseType: 'blob'
                  })
                        .then((response) => {
                              const url = window.URL
                                    .createObjectURL(new Blob([response.data]));
                              const link = document.createElement('a');
                              link.href = url;
                              link.setAttribute('download', 'image.jpg');
                              document.body.appendChild(link);
                              link.click();
                              document.body.removeChild(link);
                        })
                }
            
        }

    }
</script>
